import styled from "styled-components";


export const DivButtons = styled.div`
      grid-area: div1_buttons;
      display: flex;
      width: 100%;
      justify-content: space-between;

      height: max(12%, 29px); /* O tamanho na tela cheia  4.5vh. Como o tamanho inicial da div maior que contm os botes e os inputs  37vh, temos 12% de 37vh, que  4.44vh.
                              /* O tamanho mnimo  29px, pois o tamanho mnimo da div maior que contm os botes e os inputs  240px. Mantendo a proporo de 12%, 12% de 240px = 28.8px, arredondado para 29px */

      gap: 10px;
`;

export const DivInputs = styled.div`
      grid-area: div1_part2;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      height: max(88%, 211px); /* O tamanho na tela cheia  32.5vh. Como o tamanho inicial da div maior que contm os botes e os inputs  37vh, temos 88% de 37vh, que  32.56vh.
                              /* O tamanho mnimo  211px, pois o tamanho mnimo da div maior que contm os botes e os inputs  240px. Mantendo a proporo de 88%, 88% de 240px = 211.2px, arredondado para 211px */

      width: 100%;

      #div1_part2_inputs{
          width: 100%;
        }
`;



