import {
  Overlay,
  Window,
  CloseButton,
  Title,
  SubTitle,
  Paragraph,
  FooterNote,
} from './styles';

interface HelpPopUpProps {
  show: boolean;
  setShow: (show: boolean) => void;
}

export default function HelpPopUp({ show, setShow }: HelpPopUpProps) {
  if (!show) return null;

  return (
    <Overlay>
      <Window>
        <Title>Ajuda: Edio do Grafo de Estados</Title>

        <Paragraph>
          Esta aplicao permite criar e editar Mquinas de Turing por meio da manipulao visual de um grafo de estados. Abaixo esto as funcionalidades das ferramentas disponveis.
        </Paragraph>

        <SubTitle>Ferramenta Padro (Seleo e Edio)</SubTitle>
        <ul>
          <li><strong>Adicionar estados:</strong> clique duas vezes em um espao vazio.</li>
          <li><strong>Mover:</strong> arraste estados ou textos de transio.</li>
          <li><strong>Adicionar transies:</strong>
            <ul>
              <li>Clique na borda de um estado e arraste at o estado destino.</li>
              <li> possvel conectar o estado a si mesmo.</li>
            </ul>
          </li>
          <li><strong>Editar textos:</strong>
            <ul>
              <li>D dois cliques no nome do estado para alter-lo.</li>
              <li>D dois cliques sobre o texto da transio para edit-lo. O padro para o texto da transio : "leitura, escrita, direo"</li>
            </ul>
          </li>
          <li><strong>Selecionar e modificar:</strong>
            <ul>
              <li>Um clique seleciona uma clula.</li>
              <li>Pressione <kbd>Delete</kbd> para remov-la.</li>
              <li>Ao selecionar um estado:
                <ul>
                  <li>Clique em "Final" para alternar estado final.</li>
                  <li>Clique em "Inicial" para definir como estado inicial.</li>
                </ul>
              </li>
            </ul>
          </li>
        </ul>

        <SubTitle>Ferramenta de Seleo Mltipla</SubTitle>
        <ul>
          <li>Arraste para selecionar mltiplos elementos.</li>
          <li>Pressione <kbd>delete</kbd> para remov-los.</li>
          <li>Arraste para mov-los em grupo.</li>
        </ul>

        <SubTitle>Ferramenta Redirecionar Transio</SubTitle>
        <ul>
          <li>Clique em uma transio e arraste at outro estado.</li>
          <li>Se soltar fora de um destino vlido, ela volta ao original.</li>
        </ul>

        <FooterNote>
          Para mais informaes ou para relatar algum problema, contate-nos pelo email pet@inf.ufrgs.br
        </FooterNote>

        <CloseButton onClick={() => setShow(false)}></CloseButton>
      </Window>
    </Overlay>
  );
}
