import { TextoErro } from "./styled";
import { useStateContext } from "../../ContextProvider";

const ValidationMessage = () => {
    const {inputStates} = useStateContext();
    const {errors} = inputStates;
    
    return (
        <>
        {errors.uniqueStates?                          null : <TextoErro>No pode haver repetio nos estados!</TextoErro>                              }
        {errors.validInitialState?                     null : <TextoErro>O estado inicial deve pertencer ao conjunto de estados!</TextoErro>            }
        {errors.validFinalStates?                      null : <TextoErro>Os estados finais devem pertencer ao conjunto de estados!</TextoErro>          }
        {errors.uniqueAlphabetSymbols?                 null : <TextoErro>No pode haver repetio no alfabeto de entrada!</TextoErro>                   }
        {errors.disjointAlphabets?                     null : <TextoErro>Alfabeto auxiliar e alfabeto de entrada devem ser disjuntos!</TextoErro>       }
        {errors.alphabetDoesNotHaveStart?              null : <TextoErro>Alfabeto de entrada no deve conter smbolo de de incio de fita!</TextoErro>  }
        {errors.alphabetDoesNotHaveBlank?              null : <TextoErro>Alfabeto de entrada no deve conter smbolo de branco!</TextoErro>             }
        {errors.auxiliaryAlphabetDoesNotHaveStart?     null : <TextoErro>Alfabeto auxiliar no deve conter o smbolo de incio de fita!</TextoErro>     }
        {errors.auxiliaryAlphabetDoesNotHaveBlank?     null : <TextoErro>Alfabeto auxiliar no deve conter smbolo de branco!</TextoErro>               }
        {errors.undefinedIsNotASymbol?                 null : <TextoErro>"undefined" no pode ser um smbolo da mquina!</TextoErro>                    }
        {errors.onlyValidSymbols?                      null : <TextoErro>Os caracteres "[" e "]" no podem ser usados nos smbolos do alfabeto!</TextoErro>   }
        {errors.initSymbolDefined?                   null : <TextoErro>O smbolo inicial deve ser definido!</TextoErro>}
        {errors.blankSymbolDefined?                   null : <TextoErro>O smbolo de branco deve ser definido!</TextoErro>}
        </>
    )
}

export default ValidationMessage;